    sunChart: null,
    weatherChart: null,
    weatherChartOption: null,
    waterLevelPerc: 50,
    solarPower: 8,
    starPath: 'path://m 77.107143,118.59523 0.75595,-32.883922 18.520837,26.080362 24.56845,-23.812504 -4.53572,32.505944 31.08854,-3.21279 -24.47395,20.97767 24.75744,24.94643 -34.01786,-4.15774 0.37798,34.39584 -21.166668,-27.21429 -24.568454,22.3006 5.669644,-31.37202 -30.238095,2.64583 23.434524,-21.16667 -22.67857,-24.19047 z',
    solarTrajectoryOption: 1,
    solarTrajectory: [[{coords: [[3,10],[4,8.2]]},{coords: [[4,10],[5,8.2]]},{coords: [[5,10],[6,8.2]]},{coords: [[6,10],[7,8.2]]},{coords: [[7,10],[8,8.2]]},{coords: [[8,10],[9,8.2]]},{coords: [[9,10],[10,8.2]]}],
