        color:'blue',
        grid: {
          bottom: 0,
          left: 0,
          top: 0,
          right: 0,
        },
        xAxis: {
          data: ['Water'],
          show: false,
        },
        yAxis: {
          show: false,
          min: 0,
          max: 100,
        },
