          {
            type: 'bar',
            data: [this.waterLevelPerc],
          },
        ],
      };
      this.waterChart.setOption(waterChartOption,true)
    },
    refreshSunChart(){
      const sunChartOption = {
        grid: {
          top: 5,
        },
        xAxis: {
            type: 'value',
            show: false,
            min: 0,
            max: 10,
        },
        yAxis: {
            min: 0,
            max: 10,
            show: false,
        },
        series: [
        {
            name: 'tail',
            type: 'lines',
            coordinateSystem: 'cartesian2d',
            zlevel: 1,
            effect: {
                show: true,
                period: 10-this.solarPower,
                trailLength: .8,
                color: '#ffa022',
                symbolSize: 3
            },
            lineStyle: {
                normal: {
                    color: '#ffa022',
                    width: 0,
                    curveness: 0.05
                }
            },
            data: this.solarTrajectory[this.solarTrajectoryOption-1]
        },
