                symbolSize: 15
            },
            lineStyle: {
                normal: {
                    color: '#ffa022',
                    width: 0,
                    opacity: 0.6,
                    curveness: 0.05
                }
            },
            data: this.solarTrajectory[this.solarTrajectoryOption-1]
        },
