              effect: {
                  show: true, constantSpeed: 60, trailLength: .3, color: 'blue', symbolSize: 1
              },
              lineStyle: {
                  normal: {
                      color: 'blue', width: 0, curveness: 0
                  }
              },
              data: this.rainTrajectory3b
          },
          ],
        };
        this.weatherChart.setOption(this.weatherChartOption,false)
      }
      if(this.dayNightOption===0){
        this.refreshSunChart();
      }else if(this.dayNightOption===1){
        this.solarPower = 0;
        console.log('nighttime')
        let sunChartOption = this.sunChart.getOption()
        sunChartOption.series = [];
        this.sunChart.setOption(sunChartOption,false)
      }

